$articleBox-padding:15px;
$articleBoxTitleHeight:$font-size-medium*2*3;
$articleBoxHeader-marginBottom:10px;
$categoryLineHeight:20px;
$numberFontSize:13px;
$thumbnailMarginBottom:10px;


.articleBox-base{
  padding-top:$articleBox-padding ;
  padding-bottom:$articleBox-padding ;

}

.articleBox{
  @extend .articleBox-base;
  border-top:solid 1px $color-plart-gray;

  &.--big{
    border-top-width:0;
    margin-bottom: 1px;
    padding-top: 0px;
  }

  &.--smalltop{
    border-top-width:0;
    margin-bottom: 1px;
    padding-top: 0px;
      @include media($medium-screen-down){
      border-top:solid 1px $color-plart-gray;
      padding: 15px 0px 15px 0px;
    }
  }

}


.articleBox__header{
  margin-bottom: $articleBoxHeader-marginBottom;
  padding:0px;
  overflow: hidden;

  &.--unvisible{
    visibility:hidden;
  }
}

.articleBox__category{
  float: left;
  font-size: $font-size-medium;
  letter-spacing: $small-letter-spacing;
  line-height: $categoryLineHeight;
  font-family: $heading-font-family;

  vertical-align:bottom;
  &.--unvisible{
    visibility:hidden;
  }

  @include media($medium-screen-down) {
    line-height:1;
  }
}

.articleBox__category--ART{
  display:inline;
  font-family:$heading-font-family;
  font-weight:$heading-font-weight;

  margin-right: 4px;
}

.articleBox__category--cross{
  display:inline;
  margin-right: 4px;
  font-family:$heading-font-family;
}

.articleBox__category--genre{
  display:inline;

}

.articleBox__number{
  float: right;
  background:$color-plart-yellow;
  line-height: $categoryLineHeight;
  padding-left: $categoryLineHeight - $numberFontSize;
  padding-right: $categoryLineHeight - $numberFontSize;
  padding-top:3px;
  padding-bottom:3px;


  &.--unvisible{
    visibility:hidden;
  }

  @include media($medium-screen-down) {
    float:left;
    margin-top:$articleBoxHeader-marginBottom;
    clear:both;
  }

}



.thumbnail-base{

  clear:both;
  display: block;

  width: 100%;
  margin-bottom: $thumbnailMarginBottom;
  padding:0px;

  vertical-align: bottom;
}
.articleBox__thumbnailBox{
  position:relative;
}
.articleBox__thumbnail{
  @extend .thumbnail-base;
}

.articleBox--big .articleBox__thumbnail{
  @extend .thumbnail-base;
}

.articleBox__coverBadge{

  position:absolute;
    right:5%;
    top:5%;


  visibility:hidden;

  &.--visible{
    visibility:visible;
  }

  &.--big{
  }

  @include media($medium-screen-down){
    transform-origin:right top;
    transform:scale(0.5);
  }
}


.articleBox__hoverCover{

  opacity:0;
  position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;

  background:rgba(0,0,0, 0.5);


  transition-property:opacity;
  transition-duration:0.5s;
  transition-timing-function:ease-in-out;

  text-align:center;
}

  a:hover .articleBox__hoverCover{
    opacity:1;
  }

.articleBox__hoverCoverLogo{
  display:block;

  width: 33.33%;
  position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;


  margin:auto;



}

.articleBox__title{
  clear:both;
  word-break: break-all;
  line-height: 1.6;
  height:$articleBoxTitleHeight;
  margin:0px;
  padding: 0px;
}
